import React from "react";
import { reduxForm, Field } from "redux-form";
import "./login-form.sass";
import {
  required,
  maxLengthCreator,
  minLengthCreator,
} from "../../untils/validators/validators";
import { Element, createField } from "../../common/FormsControls/FormsControls";
import { Button, TextField } from "@material-ui/core";
import { Input } from "./../../common/FormsControls/FormsControls";

const maxLenght = maxLengthCreator(50);
const minLenght = minLengthCreator(1);

// const input = Element("input");

let LoginForm = ({ handleSubmit, error }) => {
  return (
    <form onSubmit={handleSubmit} className="form-login">
        {createField(
          [required, maxLenght, minLenght],
          "login-form_input",
          "Email",
          "email",
          "login",
          "Email"
        )}
      {createField(
          [required, maxLenght, minLenght],
          "login-form_input",
          "Password",
          "password",
          "password",
          "password"
        )}
      <div className="login-form_checkbox">
        <Field
          className="checkbox"
          type="checkbox"
          id="form_checkbox"
          name={"rememberMe"}
          component="input"
        />
        <label htmlFor="form_checkbox" className="login-form_label"></label>
        remember me
      </div>
      {error && <div className="form-summary_error">{error}</div>}
      <div className="">
        <Button
          className="login-form_button"
          variant="contained"
          color="primary"
          type="submit"
        >
          Submit
        </Button>
      </div>
    </form>
  );
};

const LoginReduxForm = reduxForm({
  form: "login",
})(LoginForm);
export default LoginReduxForm;
